.todoElement {
    padding: 1em 0;
    margin: 1em 0;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.checkbox {
    padding: 1em;
    cursor: pointer;
    width: 20px;
    height: 20px;
    border-radius: 100px;
    border: 2px solid #222222;
    opacity: .7;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    background-color: transparent;
}

.checkbox:hover {
    background-color: #222222;
    opacity: 1;
}

.todo {
    margin: 0 1em;
    -ms-flex: 2 1;
        flex: 2 1;
    overflow: hidden;
}

.deleteTodoBtn {
    padding: 1em;
    background: url('../../../assets/img/trash-can.png') no-repeat;
    width: 20px;
    background-size: 100%;
    border: 0;
    cursor: pointer;
    opacity: .7;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.deleteTodoBtn:hover {
    opacity: 1;
}


