.form {
    border: none;
    border-bottom: 1px solid grey;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
}

.input {
    padding: .7em 0;
    margin: .7em 0;
    width: 100%;
    background-color: #ffffff;
    font-size: 1em;
    border: 0;
    border-radius: 0;
}

.submitBtn {
    cursor: pointer;
    height: 2em;
    padding: 0 1em;
    -webkit-transform: translateX(-.5em);
        -ms-transform: translateX(-.5em);
            transform: translateX(-.5em);
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.submitBtn:hover {
    -webkit-transform: translateX(.5em);
        -ms-transform: translateX(.5em);
            transform: translateX(.5em);
}

.error {
    font-weight: 400;
    color: #cc0000;
    font-size: .7em;
    text-transform: none;
}


